body {
  margin: 0;
  font-family: 'Times New Roman', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Times New Roman',
    monospace;
}

* {
    box-sizing: content-box;
}

body {
    margin: auto;
}

.firstBracket {
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: .5rem;
    margin-bottom: 0;
    max-width:85%;
    width: 600px;
    height: 50px;
  }
  
.nav {
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
}

.save {
    background-color: white;
    flex-direction: column;
    color:black;
    align-items: center;
}
.nav a {
    color: inherit;
    text-decoration: none;
}

.nav Link {
    color: inherit;
    text-decoration: none;
}

.nav li.active {
    color: rgb(149, 0, 255);
}
.nav li:hover {
    color: grey;
}

.nav ul {
    justify-content: center;
    padding: 0;
    margin: 0;
    list-style: none;
    grid-gap: calc(2rem + 2vmin);
    gap: calc(2rem + 2vmin);
    display: flex;
    font-size: calc(3px + 2vmin);
}

.about {
    background-color: white;
    margin:auto;
    height:10vh; 
}
.about p {
    font-size: medium;
    margin-left: 10%;
    margin-right: 10%;
}
.about h2 {
    display: inline;
}

.about ul {
    padding: 0;
    margin: 0;
    list-style: none;
    font-size: calc(2px + 2vmin);
}

.cal {
    background-color: black;
}

.calImage {
    margin:auto;
    width: 95%;
    max-width: 1000px;
    height: auto;
}

.agoraLogo {
    margin-top: 2rem;
    width: 152px;
    height: 30px;
  }
.App {
  position:relative;
  text-align: center;
  align-items: center;
  justify-content: center;
  grid-gap: 100px;
  gap: 100px;
  width: 100%;
}

.topBracket {
  position: relative;
  margin-top: .4rem;
  margin-left: auto;
  margin-right: auto;
  max-width:100%;
  width: 1127px;
  height: 60px;
}

.navigationDiv {
  height: 210px;
}

.bottomBracket {
  display: block;
  margin-left: auto;
  margin-right: auto;
  max-width:100%;
  width: 1127px;
  height: 60px;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

.page {
  position: absolute;
  overflow: auto;
  width: 90%;
  left:5%;
  height: auto;
  max-height: 167%;
  display: block;
  text-align: center;
}

.footersave {
  position: absolute;
  display: block;
  margin: auto;
}

.footer {
  display: block;
  position: fixed;

  width: 100%;
  bottom: 0;
  left: 0;
  height: -100px;
  color: rgb(114, 114, 114);
  font-size: calc(3px + 2vmin);
}
.App-link {
  color: #2d3233;
}

